	module.exports = {"data":{"wordpressPost":{"title":"How to deal with eye lines and wrinkles","content":"<p>Smiling at someone you love is one of life&#8217;s greatest pleasures. So much more than a simple curve of the lips &#8211; a true smile reaches the eyes for it’s here where love, warmth and kindness reveal themselves. You see all of those emotions in the laughter and love lines that form at the corners of the eyes when we smile.</p>\n<p>But when we’re not actively smiling, talking, or laughing and our faces are at rest, we want the skin around our eyes to be smooth, soft and if at all possible, free of fine lines. It’s not that we’re opposed to ageing &#8211; there’s a lot we love about it &#8211; but neither fine lines or wrinkles fall into that category.</p>\n<p>So what to do?</p>\n<p>We could get <strong>muscle freezing injections</strong> to ensure we have absolutely no movement at the corners of our eyes and therefore no visible signs of ageing. But how would we connect with those dear to us? How would we let the world know the love that we’re feeling in that very moment? It’s possible we could not and so there will be no injections for us. Not this week anyway.</p>\n<p>We’re not willing to take that chance. At least not right now.</p>\n<p>What we are doing is <strong>applying eye cream</strong> daily. There are plenty of highly innovative multi-action formulas that fight the visible signs of fatigue and ageing around the delicate eye area. The result &#8211; a more refreshed, radiant and youthful appearance &#8211; no invasive procedures required.</p>\n<p>We&#8217;re also applying <strong>creamy concealer</strong> under the eye area to lighten and brighten. We like the ones that come with <strong>light-reflecting properties</strong> so that we look more youthful and soft and well, energised.</p>\n<p>And better still, we&#8217;re applying it correctly.</p>\n<p>This means drawing an inverted triangle under the eye. The triangle should run from the inner corner of the eye downwards. It should also run from the outer corner of the eyes downwards. And these two lines meet in a point. Triangle see?</p>\n<p>Then we colour in the triangle and hey presto &#8211; young, pretty eyes.</p>\n<p><strong>One other to remember</strong> &#8230; Under-eye concealer should be one to two shades lighter than your natural skin tone. &#8220;If the concealer looks too ashy, it is too light,&#8221; advises makeup guru Bobbi Brown.</p>\n<p>&#8220;It if looks very yellow, it is too dark.&#8221;</p>\n<p><strong>And another</strong> &#8230; Applying concealer on the spot next to the inner corner of the eye will open your eye up and give you a fresh, bright-eyed look, says Bobbi.</p>\n<p>&nbsp;</p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/02/27131356/NicM_1116_R.jpg"},"categories":[{"slug":"skincare","name":"Skincare"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"f15e5bc4-fc79-56d7-b740-27a3a112bd1e"}}

